import React from "react";
import { useCheckboxInput } from "react-google-forms-hooks";

export default function CheckboxInput({ id }: { id: string }) {
  const { options } = useCheckboxInput(id);

  return (
    <>
      {options.map((o) => (
        <label key={o.id} htmlFor={o.id} className="block pt-2">
          <input
            type="checkbox"
            id={o.id}
            {...o.registerOption()}
            className="mr-3"
          />
          {o.label}
        </label>
      ))}
    </>
  );
}
